import Index from './pages/ShopIndex.vue';
import bag from './pages/ShopBag.vue';
import Detail from './pages/Detail.vue';

export default [
	{
		path: '/',
		component: Index,
		redirect: '/shopIndex',
		children: [
			{
				path: '/shopIndex',
				component: 'ShopIndex'
			},
			{
				path: '/shopBag',
				component: 'ShopBag'
			}
		]
	},
	{
		path: '/detail',
		component: 'Detail'
	}
]




