<template lang='html'>
	<div class="shop-m-index">
		<div class="shop-index-head">
			
		</div>
		<section>
			<div class="shop-index-nav">
				<ul>
					<li v-for="type in types">{{type}}</li>
				</ul>
			</div>
			
			<shop></shop>
			<fashion></fashion>
		</section>
		<div class="shop-index-foot">
			
		</div>
	</div>
</template>

<script>
	import shop from './shopIndexStore.vue';
	import fashion from './shopIndexFashion.vue';
	import Vue from 'vue';
	
	import { Loadmore } from 'mint-ui';

	Vue.component(Loadmore.name, Loadmore);
	export default {
		data: function() {
			return {
			types: ['首页','箱包','美妆','服装','腕表','配饰','鞋靴','母婴','美酒','美食','家居','电子','办公']}
		},
		components: {
		    shop:shop,
		    fashion:fashion
		}
	}
</script>



